.init-container {
  min-height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.init-content {
  max-width: 600px;
  width: 100%;
}

.welcome-section {
  text-align: center;
  margin-bottom: 40px;

  h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 15px;
  }

  p {
    font-size: 1.1rem;
    color: #666;
    line-height: 1.6;
  }
}

.input-section {
  margin-bottom: 50px;

  .full-width {
    width: 100%;
  }

  .start-button {
    width: 100%;
    height: 56px;
    font-size: 1.1rem;
    font-weight: 600;
    margin-top: 10px;

    mat-icon {
      margin-right: 8px;
    }

    mat-spinner {
      display: inline-block;
      margin-right: 8px;
    }
  }
}

.features-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-top: 30px;

  .feature {
    text-align: center;
    padding: 20px;
    background: #f8f9fa;
    border-radius: 12px;
    transition: transform 0.2s;

    &:hover {
      transform: translateY(-5px);
    }

    mat-icon {
      font-size: 48px;
      width: 48px;
      height: 48px;
      color: #667eea;
      margin-bottom: 10px;
    }

    h3 {
      font-size: 1rem;
      margin: 10px 0 5px 0;
      color: #333;
    }

    p {
      font-size: 0.875rem;
      color: #666;
      margin: 0;
    }
  }
}

@media (max-width: 768px) {
  .welcome-section h2 {
    font-size: 1.5rem;
  }

  .features-section {
    grid-template-columns: 1fr;
  }
}
